import React from 'react';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';

const Items = () => (
  <Query query={gql`{
    catalogue(url:"/products/", tenantID:"demo"){
    name
    id
    link
    type_name
    type_identifier
		content_fields

		breadcrumbs{
      name
    	breadcrumb
    }
    children{
      link
      name

      product{
        sku

        variations{
          variation_sku
          price_ex_vat

        }

      }
    }
  }}
  `}
  >
    {({loading, error, data}) => {
      if (loading) return <p>Loading...</p>;
      if (error) return <p>Error :{`${error}`}</p>;
      console.log("DATA:", data.catalogue.children[0]);

      return data.catalogue.children.map(({name, link, product}) => (
        <div key={product.sku} class="card">
          <span class="cardtitle">{`${name}`} <i class="subtitle">{`${product.sku}`}</i></span>
          <br/>
          <label>Select Version:</label>
          <select>
            {Object.keys(product.variations).map((it) =>(
              <option>{product.variations[it].variation_sku} - COST: {product.variations[it].price_ex_vat}</option>
            ))}
          </select>
        </div>
      ));
    }}
  </Query>
);

export default Items;



